import React, { lazy } from 'react';
import { Route, Switch } from 'react-router-dom';

import { CREATE_BRAND, BRAND, EDIT_BRAND } from 'src/constants/pages';
import LayoutRoute from 'src/routes/LayoutRoute';

const NotFound = lazy(() => import("../NotFound"));
//const CreateUser = lazy(() => import("./Create"));
const ListBrand = lazy(() => import("./List"));
//const UpdateUser = lazy(() => import("./Update"))

/*
            <LayoutRoute exact path={CREATE_BRAND}>
                <CreateUser />
            </LayoutRoute>

            <LayoutRoute exact path={EDIT_BRAND}>
                <UpdateUser />
            </LayoutRoute>
            */
const Brand = () => {
    return (
        <Switch>
            <Route exact path={BRAND}>
                <ListBrand />
            </Route>

            <Route component={NotFound} />
        </Switch>
    )
};

export default Brand;